import React, { useEffect, useState } from "react"

import Layout from "../components/Layout"
import NotFound from "../components/pages/NotFound"
// import Loading from "../components/Post/Loading"
import SEO from "../components/utils/SEO"
import { IBreadCrumbOption } from "../interfaces"

const PageNotFound = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "404",
			name: "Not Found",
			isActive: false,
		},
	]

	const [isMount, setMount] = useState(false)

	useEffect(() => {
		setMount(true)
	}, [])

	if (!isMount) {
		return (
			<Layout breadCrumbList={breadCrumbList}>
				<SEO></SEO>
				{/* <Loading /> */}
			</Layout>
		)
	}

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO></SEO>
			<NotFound />
		</Layout>
	)
}

export default PageNotFound
