import styled from "styled-components"

export const Container = styled.div`
	padding: 48px 0 64px 0;
	font-family: "Roboto", Sans-serif;

	h2 {
		color: #13414f;
		font-size: 26px;
		line-height: 1.4em;
		font-weight: 600;
		margin-bottom: 20px;
	}

	p {
		color: #8c8c8c;
		font-size: 15px;
		font-weight: normal;
	}

	ul {
		list-style: none;
		-webkit-column-count: 3;
		-moz-column-count: 3;
		column-count: 3;

		li {
			padding-bottom: 6px;
		}

		li a {
			color: #3b3b3b;
			font-family: "Roboto", Sans-serif;
			font-weight: 400;
			font-size: 14px;
		}
	}
`

export const Divider = styled.div`
	width: 60px;
	height: 3px;
	background-color: #e8e8e8;
	margin: 32px 0;
`

export const BigLetter = styled.h3`
	color: #115964;
	font-size: 249px;
	font-weight: bold;
	text-align: center;
`
