import styled from "styled-components"

export const Container = styled.div`
	display: flex;
	font-family: "Roboto", Sans-serif;
	font-weight: 400;
	background-color: #ededed;
	border-color: #7a7a7a;
	border-radius: 0px;
	margin-bottom: 32px;

	input[type="search"] {
		margin: 0;
		border: 0;
		padding: 0;
		display: inline-block;
		vertical-align: middle;
		white-space: normal;
		background: none;
		line-height: 1;
		min-width: 0;
		font-size: 15px;
		color: #7a7979;
		min-height: 50px;
		padding-left: 16px;
		padding-right: 16px;
		width: 100%;

		:focus {
			outline: none;
		}
	}

	button {
		min-width: 150px;
		text-transform: uppercase;
		letter-spacing: 2px;
		background-color: #f4bf8c;
		font-family: "Roboto", Sans-serif;
		font-weight: 400;
		padding-left: 16px;
		padding-right: 16px;
		font-size: 16px;
		color: #fff;
		border-radius: 0;
		border: none;

		:focus {
			outline: none;
		}
	}
`
