import React from "react"

import SearchForm from "./SearchForm"
import { Container, BigLetter, Divider } from "./styles"

export default function NotFound() {
	const links = [
		{ url: "/", title: "Página inicial" },
		{ url: "/about", title: "Quem somos" },
		{ url: "/solutions", title: "Soluções" },
		{ url: "/areas-expertise", title: "Áreas de atuação" },
		{ url: "/orcamento", title: "Orçamento" },
		{ url: "/contact", title: "Contato" },
		{ url: "/blog", title: "Blog" },
	]

	return (
		<Container className="container">
			<div className="row">
				<div className="col-md-12 col-lg-6">
					<BigLetter>404</BigLetter>
				</div>

				<div className="col-md-12 col-lg-6">
					<h2>
						Ooooops! A página que você estava procurando não pôde ser encontrada
					</h2>
					<p>Você pode navegar pelo nosso menu ou usar a barra de pesquisa:</p>

					<Divider />

					<SearchForm />

					<ul>
						{links.map(link => (
							<li key={link.url}>
								<a href={link.url}>{link.title}</a>
							</li>
						))}
					</ul>
				</div>
			</div>
		</Container>
	)
}
